import React from "react";
import { useLocation, Link } from "react-router-dom";
import homeIco from "../../assets/images/home.svg";

export default function Breadcrumb() {
  const location = useLocation();
  const pathSegments = location.pathname.split("/").filter(Boolean); // Split and clean the path

  // Determine if it's a 'trainer' path, which should be ignored in the breadcrumb
  const isTrainerPage = pathSegments[0] === "instructor";

  // If it's a 'learner-list-details' page, redirect to 'learner-list'
  if (pathSegments[1] === "learner-list-details" && pathSegments.length > 2) {
    pathSegments[1] = "learner-list"; // Modify to 'learner-list'
    pathSegments.pop(); // Remove the last segment (id like 1)
  }

  // If 'trainer' is in the path, skip it and take 'dashboard' instead
  const breadcrumbs = pathSegments
    .filter(segment => segment !== "instructor") // Remove 'trainer' from breadcrumbs
    .map((segment, index) => {
      if (segment === "dashboard" && index === 0) {
        // Avoid showing 'Dashboard' in breadcrumb if it's the root page
        return null;
      }
      return segment;
    })
    .filter(Boolean); // Filter out any null values (to avoid empty breadcrumb)

  const secondLastSegment = breadcrumbs[breadcrumbs.length - 2];
  const lastSegment = breadcrumbs[breadcrumbs.length - 1];

  return (
    <div className="breadcrumb">
      <p>
        {breadcrumbs.length > 0 && (
          <Link to="/instructor/dashboard">
            <img src={homeIco} alt="Home" />
          </Link>
        )}
        {secondLastSegment && (
          <span>
            &gt; &nbsp;
            <Link to={`/instructor/${secondLastSegment}`}>
              {secondLastSegment.replace(/-/g, " ")} {/* Optional: Replace hyphens with spaces */}
            </Link>
          </span>
        )}
        {lastSegment && (
          <span>
            &gt; &nbsp;
            {lastSegment.replace(/-/g, " ")} {/* Last segment, no link */}
          </span>
        )}
      </p>
    </div>
  );
}
